<template>
  <v-card elevation="2" style="width: 30rem; height:90vh" class="mx-auto" dark>
    <v-card-title
      class="text-center"
      style="background: black; color: white;display:block"
      >My Musics</v-card-title
    >
    <h2 class="text-center">{{currentSong.name}}</h2>
    <div class="button mx-auto mt-10" style="width:fit-content">
      <v-btn class="prev" color="primary" @click="prev">Prev </v-btn>
      <v-btn class="play mx-5 pa-7" color="primary" @click="play">{{status}} </v-btn>
      <v-btn class="next" color="primary" @click="next">Next </v-btn>
    </div>
    <h3 class="list pl-5 mt-5">The Playlist</h3>
    <list class="mx-auto text-center" :listSongs="listSongs"/>

  </v-card>
</template>

<script>
import list from './list'
export default {
    data(){
        return{
            listSongs:[
                {name:'Kataomoi',author:'例えば君',src:require('../assets/song1.mp3'),play:true},
                {name:'Polaris',author:'かってよ',src:require('../assets/song2.mp3'),play:false},
                {name:'Daoko',author:'と増え',src:require('../assets/song3.mp3'),play:false},
                {name:'Sakura',author:'ねえ',src:require('../assets/song4.mp3'),play:false},
            ],
            status:'play',
            player:new Audio(),
            currentSong:'',
        }
    },
    components:{
        list,
    },
 
    methods:{
        play(){
            if(this.status=='play'){
                this.status='pause';
                this.player.play();
            }
            else{
                this.status='play';
                this.player.pause()
            }
        },
        next(){
            var index=this.listSongs.indexOf(this.currentSong);
            this.listSongs[index].play=false;
            if(index+1==this.listSongs.length) index=-1;
            index+=1;
            this.listSongs[index].play=true;
            this.currentSong=this.listSongs[index];
            this.player.src=this.listSongs[index].src;
            this.status='play';
            this.player.pause()
        },
        prev(){
            var index=this.listSongs.indexOf(this.currentSong);
            this.listSongs[index].play=false;
            if(index==0) index=this.listSongs.length;
            index-=1;
            this.listSongs[index].play=true;
            this.currentSong=this.listSongs[index];
            this.player.src=this.listSongs[index].src;
            this.status='play';
            this.player.pause()
        }
       
    },
    mounted(){      
        this.player.src=this.listSongs[0].src;
        this.currentSong=this.listSongs[0];
    }

};
</script>

<style scoped>
.prev{
    background: linear-gradient(to top right, #ff0066 0%, #ff99cc 100%);
}
.next{
    background: linear-gradient(to top right, #ff0066 0%, #ff99cc 100%);
    
}
.play{
     background: linear-gradient(to top right, #ff0066 0%, #ff6600 100%);
}
</style>